*, *:before, *:after {
  box-sizing: border-box; }

html {
  height: 100%; }

body {
  height: 100%; }

a {
  text-decoration: none; }

ul {
  margin: 0;
  padding: 0;
  list-style-type: none; }

.container {
  margin: 0 auto;
  width: 100%; }

.container:after,
.row:after {
  content: "";
  display: table;
  clear: both; }

[class*='col-'] {
  float: left;
  padding: 0px 15px;
  width: 100%;
  min-height: 1px; }

@media only screen and (min-width: 768px) {
  .container {
    width: 95%;
    max-width: 1170px; }
  .col-sm-1 {
    width: 8.33333%; }
  .col-sm-2 {
    width: 16.66667%; }
  .col-sm-3 {
    width: 25%; }
  .col-sm-4 {
    width: 33.33333%; }
  .col-sm-5 {
    width: 41.66667%; }
  .col-sm-6 {
    width: 50%; }
  .col-sm-7 {
    width: 58.33333%; }
  .col-sm-8 {
    width: 66.66667%; }
  .col-sm-9 {
    width: 75%; }
  .col-sm-10 {
    width: 83.33333%; }
  .col-sm-11 {
    width: 91.66667%; }
  .col-sm-12 {
    width: 100%; } }

@media only screen and (min-width: 1200px) {
  .col-lg-1 {
    width: 8.33333%; }
  .col-lg-2 {
    width: 16.66667%; }
  .col-lg-3 {
    width: 25%; }
  .col-lg-4 {
    width: 33.33333%; }
  .col-lg-5 {
    width: 41.66667%; }
  .col-lg-6 {
    width: 50%; }
  .col-lg-7 {
    width: 58.33333%; }
  .col-lg-8 {
    width: 66.66667%; }
  .col-lg-9 {
    width: 75%; }
  .col-lg-10 {
    width: 83.33333%; }
  .col-lg-11 {
    width: 91.66667%; }
  .col-lg-12 {
    width: 100%; } }

body {
  font-family: 'Roboto', sans-serif;
  line-height: 1.4;
  font-size: 14px; }

h1, h2, h3, h4, h5, h6 {
  font-family: inherit;
  font-weight: 500;
  line-height: 1.1; }

h1 {
  font-size: 2.5em; }

h2 {
  font-size: 30px; }

h3 {
  font-size: 24px; }

h4 {
  font-size: 18px; }

h5 {
  font-size: 14px; }

address {
  font-style: normal;
  line-height: 1.4; }

body {
  color: #333333;
  margin: 0;
  padding: 0;
  width: 100vw;
  min-height: 100vh;
  background: #fff;
  overflow-x: hidden; }

a {
  color: #337ab7; }

a:hover {
  color: #23527c;
  text-decoration: underline;
  cursor: pointer; }

hr {
  margin-top: 20px;
  margin-bottom: 20px;
  border: 0;
  border-top: 1px solid #eeeeee; }

/****SEARCH BOX ELEMENTS****/
.input-group {
  display: table; }

.input-group-btn {
  display: table-cell;
  width: 50px; }

.btn {
  border: 1px solid transparent;
  display: inline-block;
  padding: 6px 12px;
  vertical-align: middle;
  border-radius: 4px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px; }

/****ICONS****/
/* Rules for sizing the icon. */
.material-icons.md-18 {
  font-size: 18px; }

.material-icons.md-24 {
  font-size: 24px; }

.material-icons.md-36 {
  font-size: 36px; }

.material-icons.md-48 {
  font-size: 48px; }

/* Rules for using icons as black on a light background. */
.material-icons.md-dark {
  color: rgba(0, 0, 0, 0.54); }

.material-icons.md-dark.md-inactive {
  color: rgba(0, 0, 0, 0.26); }

/* Rules for using icons as white on a dark background. */
.material-icons.md-light {
  color: white; }

.material-icons.md-light.md-inactive {
  color: rgba(255, 255, 255, 0.3); }



